<template>
    <div class="header">
        
                <header>
               
                    <div class="logo" id="home">
                        <div class="icon">
                            <img src="/images/logoCerto.png" />
                            <h4>Ultra</h4>
                        </div>
                    </div>
                     <nav>
                         <ul>
                             <li><a v-on:click="Ativar" href="#home">Home</a></li>
                             <li><a v-on:click="Ativar" href="#depoimento">Depoimento</a></li>
                             <li><a v-on:click="Ativar" href="#servicos">Planos</a></li>
                             <li><a v-on:click="Ativar" class="detailsMob" href="#contato">Contato</a></li>
                         </ul>
                     </nav>
         </header>

         <div id="mobile" class="mobile">
            <div v-on:click="Ativar" class="icone" id="icone">
                
            </div>

              <nav>
                         <ul>
                             <li><a v-on:click="Ativar" href="#home">Home</a></li>
                             <li><a v-on:click="Ativar" href="#depoimento">Depoimento</a></li>
                             <li><a v-on:click="Ativar" href="#servicos">Planos</a></li>
                             <li><a v-on:click="Ativar" class="detailsMob" href="#contato">Contato</a></li>
                         </ul>
             </nav>
         </div>
    </div>

</template>

<script>
export default {
    name:'Header',

    methods:{
        Ativar: function(){
            let menu = document.getElementById("mobile")
            menu.classList.toggle("active");
            
            let icone = document.getElementById("icone")
            icone.classList.toggle("active");
        }
    }

}
</script>

<style>
header{
    padding:30px 0px;
    background: rgb(2, 5, 32);
    width:100%;
    height: 50px;
    display:flex;
    align-items: center;
    justify-content: space-around;
    font-family: 'Source Sans Pro', sans-serif;
    border-bottom: thin solid rgba(255,255,255,0.1);
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.2);
}
.icon{
    justify-content: center;
    align-items: center;
    display: flex;
    color:var(--white);
    font-size:20px;
    text-transform: uppercase;
}
 h4{
    position: relative;
    top: 5px;
}
.icon img{
    width:42px;
    margin:0 6px;
}
header nav ul{
   display: flex;
   list-style: none;
}
header nav ul li{
    padding:0 15px;
}
header nav ul li a{
    color:var(--white);
    font-size: 1.1rem;
    text-decoration: none;
    padding: 7px 10px;
    transition: 0.4s ease;
}
header nav ul li a:hover{
    background-color: rgba(0,0,0,0.2);
    padding: 5px 8px;
    border:1px solid #ccc;
}
.details{
    background:#4B59F7;
    padding: 7px 10px;
    box-shadow: 5px 3px 4px 3px rgba(0,0,0,0.1);
    color:#fff;
    border-radius: 4px;
}

.detailsMob{
    background:#4B59F7;
    width:200px;
    padding: 7px 10px;
    box-shadow: 5px 3px 4px 3px rgba(0,0,0,0.1);
    color:#fff;
    border-radius: 4px;
}
.icone{
     height: 50px;
    width: 50px;
    padding:8px 10px;
    background: #000;
    box-shadow: 4px 5px 5px 5px rgba(0,0,0,0.4);
    background-image: url('/images/menu.png');
    background-size: 80% 80%;
    background-position: center;
    position: fixed;
    cursor: pointer;
    top:20px;
    transition: all 0.2s ease-in-out;
    right: 20px;
    background-repeat: no-repeat;
}
.icone.active{
    background-image: url('/images/close.png');
    background-size: 60% 60%;
    background-repeat: no-repeat;
}
.icone:hover{
    filter:brightness(1.5);
}

.mobile{
    position: fixed;
    top: -0px;
    right: -1500px;
    width:100%;
    height: 100%;
    background-color: rgb(6, 1, 27);
    transition: ease 0.5s;
    display: none;
}
.mobile.active{
    right:0px;
}
.mobile nav{
    height: 100%;
}

.mobile nav ul{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    list-style: none;
}
.mobile li{
    margin:23px 0px;
}
.mobile a{
    font-size: 40px;
    color:var(--white);
    text-decoration: none;
    transition: ease 0.7s all;
    padding:7px 20px;
    margin:15px 0px;
}

.icone{
    display: none;
}
.mobile a:hover{
    background-color: rgba(0,0,0,0.2);
    color:var(--blue);
}
@media(max-width:850px){
    header{
        display: none;
    }
    .icone{
        display: block;
    }
    .mobile{
        display: block;
    }
}
</style>